.contact__background {
    @include mobile {
        background-image: linear-gradient(to bottom,
                #ffffff 23%,
                #FFF5F6 23%);
    }

    @media (min-width: 768px) and (max-width: 1199.98px) {
        background-image: linear-gradient(to bottom,
                #ffffff 45%,
                #FFF5F6 45%)
    }

    @include desktop {
        background-image: linear-gradient(to bottom,
                #ffffff 20%,
                #FFF5F6 20%)
    }
}

.contact__list {
    @media screen and (min-width: 768px) and (max-width: 1199.98px) {
        display: flex;
        justify-content: center;
        gap: 32px;
    }

    @include desktop {
        display: flex;
        gap: 32px;
    }
}

.contact__item {
    padding-top: 40px;
    padding-bottom: 40px;
    border-radius: 24px;
    background-color: $button-bgc;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    font-family: $main-font-family;

    @media (min-width:320px) and (max-width: 767px) {
        display: block;
        margin-left: auto;
        margin-right: auto;
        width: 280px;
        padding-left: 32px;
        padding-right: 32px;

        &:not(:last-child) {
            margin-bottom: 20px;
        }

    }

    @media screen and (min-width: 768px) and (max-width: 1199.98px) {
        flex-basis: calc((100% - 64px) / 3);
        padding-right: 33px;
        padding-left: 33px;
        display: flex;

    }

    @include desktop {
        flex-basis: calc((100% - 32px * 2) / 3);
        padding-left: 40px;
        padding-right: 40px;
    }

}

.contact__uppertitle {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 32px;

    @include font(500, 12px, 1.8, 0.04em);
    border-radius: 4px;
    background-color: $accent-yellow;
    color: $button-bgc;

    @include mobile {
        width: 80px;
        height: 16px;
    }

    @media screen and (min-width: 768px) and (max-width: 1199.98px) {
        width: 73px;
        height: 28px;
    }

    @include desktop {
        width: 75px;
        height: 28px;
    }
}

.contact__uppertitle--clr-change {
    background-color: $accent-green;

    @include mobile {
        width: 130px;
    }

    @media screen and (min-width: 768px) {
        width: 138px;
    }
}

.contact__subtitle {
    @include font(500, 12px, 1.5, 0.04em);
    color: #000000;
}

.contact__text {
    font-family: $main-font-family;
    @include font(500, 12px, 1.5, 0.04em);
    color: $main-text-color;
    margin-top: 8px;

    @media (min-width:768px) and (max-width:1199px) {
        @include font(500, 14px, 1.6, 0.04em);
        padding-bottom: 32px;
        margin-top: 12px;
    }

    @include desktop {
        @include font(500, 16px, 1.9, 0.04em);
        padding-right: 40px;
    }

}

.contact__box {
    padding-bottom: 24px;
    margin-bottom: 24px;
    border-bottom: 1px solid #E1E1E1;

    @include mobile {
        width: 216px;
    }

    @media (min-width:768px) and (max-width:1199px) {
        width: 160px;
        margin-bottom: 32px;
    }

    @include desktop {
        width: 288px;
    }
}

.contact__time {
    font-family: $main-font-family;
    @include font(500, 12px, 1.8, 0.04em);
    color: $accent-pink;

    @media (min-width:768px) and (max-width:1199px) {
        font-size: 14px;

    }

    @include desktop {
        font-size: 16px;

    }
}

.contact__link {

    font-family: $main-font-family;
    @include font(500, 12px, 1.5, 0.04em);
    color: $main-text-color;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        color: #D46443
    }

    @include tablet {
        @include font(500, 14px, 1.8, 0.04em);
    }

    @include desktop {
        @include font(500, 16px, 1.9, 0.04em);
    }
}


.contact__btn-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 32px;

    @include tablet {
        flex-direction: row;
        align-items: center;
        justify-content: center;
        gap: 15px;
        margin-top: 60px;
    }

    @include desktop {
        gap: 12px;
        margin-top: 60px;
    }
}

.btn-item:first-child {
    @media (max-width:767px) {
        margin-bottom: 10px;
    }
}

.contact__btn {
    display: block;
    width: 161px;
    height: 44px;

    font-family: $main-font-family;
    font-style: bold;
    @include font(700, 16px, 1.3, 0.04em);

    border-radius: 22px;
    border-color: transparent;
    cursor: pointer;

}

.contact__btn--left {
    color: #ffffff;
    background-color: $main-red-color;

    @include tablet {
        width: 191px;
        height: 44px;
    }

    @include desktop {
        width: 198px;
        height: 44px;
    }
}

.contact__btn--right {
    color: $main-red-color;
    background-color: $button-bgc;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
        background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    @include tablet {
        width: 174px;
        height: 44px;
    }

    @include desktop {
        width: 168px;
        height: 44px;
    }

    &:hover,
    &:focus {
        background-color: $button-hover;
    }
}

.contact__btn--left-icon {
    stroke: $button-bgc;
}

.contact__btn--right-icon {
    stroke: $main-red-color;
}

.is-hidden-loc {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;}

.locations-modal {
    position: absolute;
    width: 100%;
    min-height: 600px;

    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
    background-color: white;
    border-radius: 5px;
    background-color:transparent;
    
  

    @media (min-width:768px) and (max-width:1199px) {
        width: 768px;
       
    }

    @include desktop {
        width: 800px;

    }}
   
    .modal-map{
    width: 100%;
    height: 600px;
    padding: 25px;
    border-radius: 5px;
      border:transparent;
     

    @media (min-width:768px) and (max-width:1199px) {
        width: 768px;
       
    }

    @include desktop {
        width: 800px;

    }

}


